import React from 'react';
import Link from 'gatsby-link';
import YouTube from 'react-youtube';
import Textloop from '../components/textloop';
//import HeroLogo from '../images/cross-stitch-site-name.png';
//L24: <h1><img src={HeroLogo} alt="Sysforge Lab" title="Sysforge Lab" /></h1>
//L29: <YouTube videoId='RtEwlMiJoug'/>
import SFMobLogo from '../images/salesforce-logo-mobile.png';
import GitHubLogo from '../images/GitHub-Mark-22px.png';
import LinkedinLogo from '../images/linkedIn.png';


export const Masthead = ({props}) =>
(
    <div className="jumbotron">
        <div className="container breadcrumb-container">
            <div className="row">
                <div className="col-sm-12">
                    <ul className="breadcrumb">
                        <li><Link to="/">Home</Link></li>
                        <li className="current-breadcrumb">Sysforge</li>
                    </ul>
                </div>
            </div>
        </div>
        <div className="container text-center">
            <div className="row vertical-align">
                <div className="caption-content">
                    <Textloop
                        children={[
                            "I'm Cloud Solution Architect, Designing Entripse App Since 14 Year",
                            "Consulting Experience at Accenture, British Airways, Direcline, T-Mobile etc.",
                            "Certified Salesforce Professional With Informatica Cloud Skills",
                            "Process Automation and Bulk/ Soap/ Rest API Integration",
                            "Architected CRM Automation, Offline Data Sync Solution with Business",
                            "Security Implementation - SSO, OAuth, 2 Way SSL, SAML Key, Certificate",
                            "Have Polyglot HandsOn Skills- Apex/ ES6/ Java/ JEE/ GRADLE/ MAVEN",
                            "In-depth Knowledge on Data Migration, Unix CLI, Outbound Messaging",
                            "Higher RoI for Business - Sonar, Jenkins, GitHub, Webdriver",
                            "Read Tech Blog & Connect to know more about Madhav Shandilya!"
                        ]}
                    />
                </div>
                <br/>
                <div className="col-sm-6">
                    <div className="youtube-embed-container">
                        <div className="embed-responsive embed-responsive-16by9">
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
);

export default Masthead;
    


